// Home Desktop
.blue-text {
  color: #769fda; }

.hero {
  background-color: #fafafa;
  &__flex {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 5rem;
    justify-content: center;
    &-text {
      &--etiqueta {
        p {
          font-size: .75rem;
          display: inline-block;
          color: #769fda;
          text-transform: uppercase;
          border: 3px #769fda solid;
          border-radius: 10rem;
          padding: .25rem 1rem;
          font-weight: 400; } }
      &--title {
        h1 {
          font-size: 2.25rem;
          font-weight: 500; } }
      &--p {
        margin-bottom: 3rem; }
      &--button {
        button {
          color: #FFFFFF;
          font-weight: 500;
          display: flex;
          background-color: #84d5dc;
          padding: .75rem 1.25rem;
          border-radius: 1rem;
          border-color: transparent;
          transition: 1s;
          &:hover {
            background-color: #33435b; }
          a {
            display: flex;
            color: #FFFFFF;
            align-items: center;
            p {
              margin: 0; }
            img {
              width: 1rem;
              height: 1rem;
              margin-left: 1.25rem; } } } } }
    &-img {
      z-index: 10;
      img {
        margin-left: 3rem;
        width: 35vw; } } }
  &__background {
    img {
      width: 35rem;
      opacity: .25; } } }

.info {
  padding: 0 5rem;
  margin-top: -3.5rem;
  margin-bottom: 5rem;
  &__grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 5rem;
    &-img {
      text-align: center;
      margin: 0 auto;
      &-none {
        display: none;
        width: 100%;
        height: 20rem;
        object-fit: cover;
        border-radius: 1.5rem; }
      p {
        margin: 1rem .5rem;
        font-size: .8rem;
        text-align: center;
        color: #33435b; } }
    &-text {
      cursor: pointer;
      &--title {
        h2 {
          margin: 0;
          font-size: 1.75rem;
          font-weight: 500; } }
      &--subtitles {
        div {
          h3 {
            color: #33435b;
            margin: 1rem 0;
            padding: 1rem;
            font-weight: 500; } } } } } }


.activo {
  background-color: #e6ffff;
  border-radius: .5rem; }

.mostrar {
  display: block; }

.responsive {
  display: none; }
