// Footer Desktop

.footer {
  background-color: #e6ffff;
  padding: 3rem 5rem;
  &__grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 2fr 1fr 1fr 1fr;
    margin-bottom: 1rem;
    grid-gap: 1.5rem; }
  &__logo {
    img {
      width: 5.25rem; } }
  &__links01 {
    color: #33435b;
    &-title {
      h4 {
        margin: 0;
        font-size: .8rem; } }
    &-text {
      font-size: .75rem; }
    &-subtitle {
      h4 {
        color: #769fda;
        font-size: .75rem;
        text-transform: uppercase;
        font-weight: 400; } }
    &-flex {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-gap: 3rem; } }
  &__rrss {
    margin-top: 1.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    p {
      font-size: .75rem;
      color: #33435b; }
    &-icons {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr;
      grid-gap: 1rem;
      a {
        img {
          width: 1rem; } } } } }
