// Header Desktop

.header {
  background-color: #FFFFFF;
  position: relative;
  margin: 0;
  &__responsive {
    display: none; }
  &__nav {
    padding: 1rem 5rem;
    display: flex;
    hr {
      display: none; } }
  &__logo {
    margin-right: 3.5rem;
    img {
      width: 5.25rem; } }
  &__menu {
    z-index: 10000;
    display: flex;
    justify-content: space-between;
    li {
      margin-right: 2rem;
      a {
        font-size: .75rem;
        transition: .5s;
        &:hover {
          color: #7bd2da; } } } }
  &__fondo {
    position: absolute;
    right: 0;
    top: 0;
    img {
      width: 35vw; } } }

